/* INPUT */

.bk-input {
  box-sizing: border-box;
  margin: 0;
  padding: 4px 11px;
  color: rgba(0, 0, 0, 0.88);
  font-size: 14px;
  line-height: 1.5714285714285714;
  list-style: none;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, 'Helvetica Neue', Arial, 'Noto Sans', sans-serif,
    'Apple Color Emoji', 'Segoe UI Emoji', 'Segoe UI Symbol', 'Noto Color Emoji';
  position: relative;
  display: inline-block;
  width: 100%;
  min-width: 0;
  background-color: #ffffff;
  background-image: none;
  border-width: 1px;
  border-style: solid;
  border-color: #d9d9d9;
  border-radius: 6px;
  transition: all 0.2s;
}

.bk-input:focus-within {
  border-color: #1677ff;
  box-shadow: 0 0 0 2px rgba(5, 145, 255, 0.1);
  outline: 0;
}

.bk-input:hover {
  border-color: #4096ff;
}

input.bk-input::placeholder {
  color: rgba(0, 0, 0, 0.25);
  user-select: none;
  text-overflow: ellipsis;
}

input.bk-input:placeholder-shown {
  text-overflow: ellipsis;
}

.bk-input-group-wrapper {
  display: inline-block;
  width: 100%;
  text-align: start;
  vertical-align: top;
}

.bk-input-wrapper.bk-input-group {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  color: rgba(0, 0, 0, 0.88);
  font-size: 14px;
  line-height: 1.5714285714285714;
  list-style: none;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, 'Helvetica Neue', Arial, 'Noto Sans', sans-serif,
    'Apple Color Emoji', 'Segoe UI Emoji', 'Segoe UI Symbol', 'Noto Color Emoji';
  position: relative;
  display: table;
  width: 100%;
  border-collapse: separate;
  border-spacing: 0;
}

.bk-input-group-addon:first-child {
  border-start-end-radius: 0;
  border-end-end-radius: 0;
  border-inline-end: 0;
}

.bk-input-group-addon:last-child {
  border-start-start-radius: 0;
  border-end-start-radius: 0;
  border-inline-start: 0;
}

.bk-input-group-addon {
  position: relative;
  padding: 0 11px;
  color: rgba(0, 0, 0, 0.88);
  font-weight: normal;
  font-size: 14px;
  text-align: center;
  background-color: rgba(0, 0, 0, 0.02);
  border: 1px solid #d9d9d9;
  border-radius: 6px;
  transition: all 0.3s;
  line-height: 1;
  display: table-cell;
  width: 1px;
  white-space: nowrap;
  vertical-align: middle;
}

.bk-input-wrapper.bk-input-group input.bk-input {
  border-radius: 0;
  width: 100%;
  margin-bottom: 0;
  text-align: inherit;
  display: table-cell;
}

.bk-input-affix-wrapper:not(:last-child) {
  border-start-end-radius: 0;
  border-end-end-radius: 0;
}

.bk-input-affix-wrapper:not(:first-child) {
  border-start-start-radius: 0;
  border-end-start-radius: 0;
}

.bk-input-affix-wrapper {
  position: relative;
  display: inline-flex;
  width: 100%;
  min-width: 0;
  padding: 4px 11px;
  color: rgba(0, 0, 0, 0.88);
  font-size: 14px;
  line-height: 1.5714285714285714;
  background-color: #ffffff;
  background-image: none;
  border-width: 1px;
  border-style: solid;
  border-color: #d9d9d9;
  border-radius: 6px;
  transition: all 0.2s;
  box-sizing: border-box;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, 'Helvetica Neue', Arial, 'Noto Sans', sans-serif,
    'Apple Color Emoji', 'Segoe UI Emoji', 'Segoe UI Symbol', 'Noto Color Emoji';
}

.bk-input-affix-wrapper.inline {
  border-radius: 6px;
}

span.bk-input-affix-wrapper::before {
  display: inline-block;
  width: 0;
  visibility: hidden;
  content: '\a0';
  box-sizing: border-box;
}

span.bk-input-prefix {
  margin-inline-end: 4px;
  display: flex;
  flex: none;
  align-items: center;
  box-sizing: border-box;
}

span.bk-input-suffix {
  margin-inline-start: 4px;
  display: flex;
  flex: none;
  align-items: center;
